
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Card from 'vux/src/components/card/index.vue'

import XSwitch from 'c%/switch.vue'
import MyRater from 'c%/MyRater.vue'

export default{
mixins: [ common,globals ],
components: {
Card,
MyRater,
XSwitch
},
data () {
return {
mapRel: {0:'-',1:'自己',2:'爸爸',3:'妈妈',4:'其他'},
info: {},
id: this.$route.params.id,
followList: [],
deputy: []
}
},
mounted() {
this.init_data()
},
methods: {
init_data() {
this.showLoading()
this.$rest(this.data_uri).get()
.success(data=>{
this.info = data.customer
this.deputy = data.deputy
this.getFollowList()
this.hideLoading()
})
},
getFollowList () {
let uri = 'customer_follow_up?cu_id='+this.id
this.$rest(uri).get()
.success(data=>{
this.followList = data.list
})
}
},
computed: {
data_uri () {
return 'customers/'+this.id
}
}
}




